import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import ManageUsers from "./components/ManageUsers/ManageUsers";

import config from 'react-global-configuration';

config.set({
    apiUrl: "http://localhost:8000",
});

/*<BrowserDetection>
    { browserHandler }
</BrowserDetection>*/
function detectIE() {
    var ua = window.navigator.userAgent;

    // Test values; Uncomment to check result …

    // IE 10
    // ua = 'Mozilla/5.0 (compatible; MSIE 10.0; Windows NT 6.2; Trident/6.0)';

    // IE 11
    // ua = 'Mozilla/5.0 (Windows NT 6.3; Trident/7.0; rv:11.0) like Gecko';

    // Edge 12 (Spartan)
    // ua = 'Mozilla/5.0 (Windows NT 10.0; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/39.0.2171.71 Safari/537.36 Edge/12.0';

    // Edge 13
    // ua = 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/46.0.2486.0 Safari/537.36 Edge/13.10586';

    var msie = ua.indexOf('MSIE ');
    if (msie > 0) {
        // IE 10 or older => return version number
        return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
    }

    var trident = ua.indexOf('Trident/');
    if (trident > 0) {
        // IE 11 => return version number
        var rv = ua.indexOf('rv:');
        return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
    }

    var edge = ua.indexOf('Edge/');
    if (edge > 0) {
        // Edge (IE 12+) => return version number
        return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
    }

    // other browser
    return false;
}
var version = detectIE();
var html = {};

if (version === false) {
    html =
        <MuiThemeProvider>
            <Router>
                <div>
                    <Route exact path="/" component={(s) => rend("home")} />
                    <Route path="/manageUsers" component={(s) => rend("manageUsers")} />
                    <Route path="/registrer" component={(s) => rend("registrer")} />
                    <Route path="/login" component={(s) => rend("login")} />
                    <Route path="/addProduct" component={(s) => rend("addProduct")} />
                </div>
            </Router>
        </MuiThemeProvider>

} else {
                    html = <div>Internet Explorer is not supported</div>;
}
ReactDOM.render(
    <div>{html}</div>
    , document.getElementById('root'));

function rend(str) {
    return (
        <App rend={str}/>
    )
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
