import React, {Component} from 'react';
import Cookies from 'universal-cookie';
import Fab from '@material-ui/core/Fab';
import AddIcon from 'material-ui-icons/Add';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper from '@material-ui/core/Paper';
import Draggable from 'react-draggable';
import withStyles from "@material-ui/core/styles/withStyles";
import TextField from "@material-ui/core/TextField";
import Divider from "@material-ui/core/es/Divider/Divider";
import Select from "../ManageUsers/ManageUsers";

function PaperComponent(props) {
    return (
        <Draggable>
            <Paper {...props} />
        </Draggable>
    );
}

const cookies = new Cookies();

const styles = theme => ({
    fab: {
        position: 'absolute',
        bottom: theme.spacing.unit * 6,
        right: theme.spacing.unit * 6,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
    },
});

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            addProductDialog: {
                open: false,
                title: '',
            }
        };
    }

    addProduct = () => {
        this.setState({addProductDialog: {open: true}});
    };

    handleAddProductDialogClose = () => {
        this.setState({addProductDialog: {open: false}});
    };

    handleAddProduct = () => {
        this.setState({addProductDialog: {open: true}});
    };

    renderButton() {
        const {classes, theme} = this.props;
        if (cookies.get("token") !== undefined) {
            return (<Fab color="primary" aria-label="Add" className={classes.fab} onClick={() => {window.location.pathname = "addProduct"}}>
                <AddIcon color={"#fafafa"}/>
            </Fab>)
        } else {
            return (<div></div>);
        }
    }

    handleTextChange = name => event => {
        this.setState({
            addProductDialog: {
                [name]: event.target.value
            },
        });
    };

    render() {
        const {classes, theme} = this.props;

        let addProductDialog = () => {
            return (
                this.state.addProductDialog.open ? (<div style={{"width": "900px"}}>
                    <Dialog
                        disableEnforceFocus
                        onClose={(e) => this.handleAddProductDialogClose()}
                        aria-labelledby="customized-dialog-title"
                        open={this.state.addProductDialog.open}
                    >

                        <DialogTitle id="customized-dialog-title" onClose={(e) => this.handleAddProductDialogClose()}>
                            Add product
                        </DialogTitle>
                        <DialogContent style={{"width": "900px"}}>
                            <div>

                                <TextField
                                    id="standard-name"
                                    label="Title"
                                    className={classes.textField}
                                    value={this.state.addProductDialog.title}
                                    onChange={(e,newValue) => { this.setState({addProductDialog: { title: newValue, open: true }}) }}
                                    margin="normal"
                                />
                                <br/>
                                <TextField
                                    placeholder="Text"
                                    multiline={true}
                                    rows={300}
                                />
                            </div>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={this.handleAddProduct} color="#0783F5"
                                    style={{"fontWeight": "bold", "color": "#2196f3", "fontSize": "0.875rem"}}>
                                Add
                            </Button>
                        </DialogActions>
                    </Dialog>

                </div>) : (<div></div>)
            );
        };

        return (
            <div className="container center">
                {this.renderButton()}
                {addProductDialog()}
            </div>
        );
    }
}

//export default Home;

export default withStyles(styles, {withTheme: true})(Home);